import React from 'react';
import ReactSelect from 'react-select';
import { Row, Col, Card, Form, Button, Table, FormControl, DropdownButton, Dropdown, Badge } from 'react-bootstrap';
import Aux from "../../hoc/_Aux";
import DatePicker from 'react-datepicker';

import Select from 'react-select';
import AsyncSelect from 'react-select/async';

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Loader from '../../App/components/Reusables/Loader';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { validatePurchaseOrderCreation, validatePurchaseItemsCreation } from '../../validation/purchase';
import { getPurchaseOrderStatus } from '../../api/misc';
import { deleteItems, getItems } from '../../api/inventory/itemApi';
import { deleteVendors, getVendors } from '../../api/inventory/vendorApi';
import isEmpty from '../../validation/isEmpty';
import { createPurchaseOrder, getPurchaseOrder, updatePurchaseOrder, } from '../../api/inventory/purchaseOrder';
import { createPurchaseItem, updatePurchaseItem, deletePurchaseItems } from '../../api/inventory/purchaseItem';
import Num from '../../App/components/Reusables/Num';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import CustomSelectMenu from '../../App/components/Reusables/CustomSelectMenu';
import CreateVendorModal from '../Vendors/CreateVendorModal';
import store from '../../redux/store';
import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import DEMO from '../../globals/constant';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';



const purchaseItemModel = {
    item_id: '',
    itemName: '',
    quantity: '',
    defective: 0,
    price: '',
    itemSubtotal: '',
    discount: 0,
    discountType: 'percent',
    total: '',
    itemStatus: ''
}


const Update = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [ing, setIng] = React.useState(false)
    const [orderStatuses, setOrderStatuses] = React.useState([])
    const [receivedPurchaseItems, setReceivedPurchaseItems] = React.useState([]);

    const [customerInputText, setCustomerInputText] = React.useState('')
    const [customerOptionsMaxLength, setCustomerOptionsMaxLength] = React.useState(0)
    const [defaultCustomerOptions, setDefaultCustomerOptions] = React.useState([])

    const [vendorInputText, setVendorInputText] = React.useState('');
    const [defaultVendorOptions, setDefaultVendorOptions] = React.useState([])
    const [vendorOptionsMaxLength, setVendorOptionsMaxLength] = React.useState(0)

    const [itemInputText, setItemInputText] = React.useState('');
    const [itemOptionsMaxLength, setItemOptionsMaxLength] = React.useState(0);
    const [defaultItemOptions, setDefaultItemOptions] = React.useState([]);

    const [purchaseOrderData, setPurchaseOrderData] = React.useState({
        id: '',
        vendor_id: null,
        vendorName: '',
        invoiceNo: '',
        invoicedOn: '',
        completedOn: '',
        orderStatus: '',
        subTotal: 0,
        extraDiscount: 0,
        extraDiscountType: 'percent',
        extraDiscountAmount: 0,
        total: 0,
    })

    const [purchaseItemsData, setPurchaseItemsData] = React.useState([{ ...purchaseItemModel }]);
    const [itemIdsToBeDeleted, setItemIdsToBeDeleted] = React.useState([]);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, purchaseItemId: null })

    const [orderErrors, setOrderErrors] = React.useState({})
    const [itemErrors, setItemErrors] = React.useState([])

    const getItems_ = async (signal) => {
        console.log('api call fired to get items')
        await getItems({
            "action": 'get',
            "filter": 'none',
            "start": defaultItemOptions.length,
            "end": defaultItemOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultItemOptions([
                        ...defaultItemOptions,
                        ...res.json.items.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, price: itm.average_cost_price, weight: itm.weight || 0 }))
                    ]);
                    setItemOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsItems', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    const getVendors_ = async (signal) => {
        await getVendors({
            "action": 'get',
            "filter": 'none',
            "start": defaultVendorOptions.length,
            "end": defaultVendorOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultVendorOptions([
                        ...defaultVendorOptions,
                        ...res.json.vendors.map(instance => ({ label: `${instance.first_name} ${instance.middle_name} ${instance.last_name}`, value: instance.id }))
                    ]);
                    setVendorOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsVendors', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }


    //Component did mount 
    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            await getPurchaseOrderStatus(null, controller.signal)
                .then((res) => {
                    if (res.json.status) {
                        setOrderStatuses(res.json.data);
                        ladder++;
                    }
                }).catch((err) => {
                    console.log(err)
                });

            const vendorsJson = { action: 'get', filter: 'none', start: 0, end: 999999 }
            await getVendors(vendorsJson, controller.signal)
                .then((res) => {
                    if (res.json.status) {
                        // setVendorOptions([
                        //     ...res.json.vendors.map((ven, index) => ({ label: `${ven.first_name} ${ven.middle_name} ${ven.last_name}`, value: ven.id }))
                        // ])
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });

            getItems_(controller.signal);
            getVendors_(controller.signal)

            await getPurchaseOrder({ action: 'get', purchase_order_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        const purchaseOrder = res.json.p_order[0];
                        const purchaseItems = res.json.p_items;
                        await setReceivedPurchaseItems([...purchaseItems])
                        await setPurchaseOrderData({
                            ...purchaseOrderData,
                            id: purchaseOrder.id,
                            vendor_id: purchaseOrder.vendor,
                            vendorName: purchaseOrder.vendor_name,
                            invoiceNo: purchaseOrder.third_party_invoice_number,
                            invoicedOn: new Date(purchaseOrder.invoiced_on),
                            completedOn: new Date(purchaseOrder.completed_on),
                            orderStatus: purchaseOrder.status,
                            extraDiscount: purchaseOrder.discount,
                            extraDiscountType: purchaseOrder.discount_type,
                        })

                        const tempPurchaseItems = []
                        for (let pitm of purchaseItems) {

                            let tempitm = {
                                id: pitm.id,
                                item_id: pitm.item,
                                itemName: pitm.item_name,
                                quantity: pitm.quantity,
                                defective: pitm.defective,
                                price: pitm.non_discount_price,
                                itemSubtotal: pitm.non_discount_price * pitm.quantity,
                                discount: pitm.discount,
                                discountType: pitm.discount_type,
                                total: pitm.purchase_price * pitm.quantity,
                                itemStatus: pitm.status
                            }
                            tempPurchaseItems.push(tempitm);
                        }
                        await setPurchaseItemsData([...tempPurchaseItems]);
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
            if (ladder === 3) {
                setLoading(false)
            }
        })();
        return () => controller.abort();
    }, []);
    // console.log(purchaseItemsData)  

    React.useEffect(() => {
        let orderSubtotal = 0;
        for (let anItem of purchaseItemsData) {
            orderSubtotal += anItem.total
        }
        setPurchaseOrderData({ ...purchaseOrderData, subTotal: orderSubtotal || 0 })
    }, [purchaseItemsData])


    React.useEffect(() => {
        let totalAmt = 0;
        const extraDiscountAmt = purchaseOrderData.extraDiscountType === 'percent' ?
            purchaseOrderData.subTotal * 0.01 * purchaseOrderData.extraDiscount || 0 :
            purchaseOrderData.extraDiscount || 0;
        totalAmt = !isEmpty(purchaseOrderData.extraDiscount) ?
            purchaseOrderData.subTotal - extraDiscountAmt :
            0;
        setPurchaseOrderData({ ...purchaseOrderData, extraDiscountAmount: extraDiscountAmt || 0, total: totalAmt })
    }, [purchaseOrderData.subTotal, purchaseOrderData.extraDiscount, purchaseOrderData.extraDiscountType]);


    const amendOrderErrorOf = (field) => {
        setOrderErrors({ ...orderErrors, [field]: '' })
    }

    const amendItemsErrorOf = (row, field) => {
        let tempItemsError = itemErrors;
        if (tempItemsError[row]) {
            tempItemsError[row][field] = '';
            setItemErrors([...tempItemsError])
        }
    }

    const loadItemOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getItems(requestJson)
            .then((res) => {
                if (res.json.status) {
                    return res.json.items.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });

    }


    const loadVendorOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', first_name: inputText }
        return getVendors(requestJson)
            .then((res) => {
                if (res.json.status) {
                    return res.json.vendors.map((ven, index) => ({ label: `${ven.first_name} ${ven.middle_name} ${ven.last_name}`, value: ven.id }));
                }
            }).catch((err) => {
                console.log(err)
            });
    }


    const _vendorInputChange = (inputText) => {
        setVendorInputText(inputText)
    }
    const _scrollVendorMenuToBottom = (e) => {
        e.preventDefault();
        if (vendorOptionsMaxLength > defaultVendorOptions.length && !vendorInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsVendors', true]
            })
            getVendors_();
        }
    }

    const _selectedVendorChange = ven => {
        amendOrderErrorOf('vendor_id')
        setPurchaseOrderData({ ...purchaseOrderData, vendor_id: ven.value, vendorName: ven.label })
    };

    const _invoicedOnChange = (selectedDate) => {
        amendOrderErrorOf('invoicedOn')
        setPurchaseOrderData({ ...purchaseOrderData, invoicedOn: selectedDate })
    }

    const _completedOnChange = (selectedDate) => {
        amendOrderErrorOf('completedOn')
        setPurchaseOrderData({ ...purchaseOrderData, completedOn: selectedDate })
    }



    const _purchaseOrderDataChange = (e) => {
        e.preventDefault();
        const type = e.target.type
        const target = e.target.name
        const value = e.target.value
        amendOrderErrorOf(target)
        setPurchaseOrderData({ ...purchaseOrderData, [target]: type === 'number' ? parseFloat(value) || '' : value })

    }

    const _itemInputChange = (itemInputText) => {
        setItemInputText(itemInputText);
    }

    const _scrollItemMenuToBottom = (e) => {
        e.preventDefault();
        if (itemOptionsMaxLength > defaultItemOptions.length && !itemInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsItems', true]
            })
            getItems_();
        }
    }



    const _selectedItemChange = (row, itm) => {
        amendItemsErrorOf(row, 'item_id')
        const tempItemsData = purchaseItemsData;
        tempItemsData[row].item_id = itm.value
        tempItemsData[row].itemName = itm.label
        setPurchaseItemsData([...tempItemsData])
    }

    const _purchaseItemsDataChange = (row, e) => {
        const target = e.target.name;
        const tempItemsData = purchaseItemsData;
        amendItemsErrorOf(row, target)
        tempItemsData[row][e.target.name] = e.target.type === 'number' ? parseFloat(e.target.value) || "" : e.target.value;
        if (target != 'status') {
            if (
                (
                    !isEmpty(tempItemsData[row].price) &&
                    !isEmpty(tempItemsData[row].quantity))
            ) {
                tempItemsData[row].total = tempItemsData[row].discountType === 'fixed' ?
                    tempItemsData[row].price * tempItemsData[row].quantity - (tempItemsData[row].discount * tempItemsData[row].quantity || 0) :
                    tempItemsData[row].price * tempItemsData[row].quantity - tempItemsData[row].price * 0.01 * tempItemsData[row].quantity * (tempItemsData[row].discount || 0)
                tempItemsData[row].itemSubtotal = tempItemsData[row].price * tempItemsData[row].quantity;
                setPurchaseItemsData([...purchaseItemsData])
            }
        }
        setPurchaseItemsData([...tempItemsData]);


    }

    const _addPurchaseItem = (e) => {
        e.preventDefault();
        setPurchaseItemsData([...purchaseItemsData, { ...purchaseItemModel }])
    }





    const _removePurchaseItem = (removedItemIndex, removedItemId) => {
        if (!isEmpty(removedItemId)) {
            setDeleteData({ ...deleteData, showModal: true, itemIndex: removedItemIndex, itemId: removedItemId, })
            return;
        }
        const tempItemsData = purchaseItemsData;
        tempItemsData.splice(removedItemIndex, 1);
        setPurchaseItemsData([...tempItemsData]);
    }

    const _deletePurchaseItem = async () => {
        const tempItemsData = purchaseItemsData;
        tempItemsData.splice(deleteData.itemIndex, 1);
        await setPurchaseItemsData([...tempItemsData]);

        await setItemIdsToBeDeleted([...itemIdsToBeDeleted, deleteData.itemId]);
        await setDeleteData({ ...deleteData, showModal: false, itemIndex: null, itemId: null })
    }

    const _update = async (e) => {
        e.preventDefault();
        const orderValidity = validatePurchaseOrderCreation(purchaseOrderData);
        const itemsValidity = validatePurchaseItemsCreation(purchaseItemsData)
        if (!orderValidity.isValid) {
            setOrderErrors({ ...orderValidity.errors });
            Jhilikka.fire({ icon: 'error', title: 'Fix errros and retry!', background: '#ffe5d6' })
            return;
        }
        if (!itemsValidity.isValid) {
            setItemErrors([...itemsValidity.errors]);
            Jhilikka.fire({ icon: 'error', title: 'Fix errros and retry!', background: '#ffe5d6' })
            return;
        }

        const {
            id,
            vendor_id, //will be a vendor object
            invoiceNo,
            invoicedOn,
            completedOn,
            orderStatus,
            extraDiscount,
            extraDiscountType,
            extraDiscountAmount,
        } = purchaseOrderData


        const updateOrderJson = {
            'action': 'update',
            'purchase_order_id': id,
            'third_party_invoice_number': invoiceNo,
            'invoiced_on': invoicedOn,
            'completed_on': completedOn,
            'discount_type': extraDiscountType,
            'discount': extraDiscount,
            'bill_received': false,
            'vendor': vendor_id,
            'status': orderStatus,
        }


        //purchase items to be updated
        const itemsToBeUpdated = purchaseItemsData.filter((updateItem, updateIndex) => {
            if (!isEmpty(receivedPurchaseItems.find((recItem, recIndex) => updateItem.id === recItem.id))) return updateItem;
        })

        //purchase items to be added
        const itemsToBeAdded = purchaseItemsData.filter(updateItem => {
            if (isEmpty(receivedPurchaseItems.find(recItem => recItem.id === updateItem.id))) return updateItem
        })


        const itemsAddJson = { action: 'add_multiple', purchase_order: purchaseOrderData.id, purchase_items: [] };
        for (let anItem of itemsToBeAdded) {
            itemsAddJson.purchase_items.push({
                'item': anItem.item_id,
                'quantity': anItem.quantity,
                'defective': anItem.defective,
                'non_discount_price': anItem.price,
                'discount_type': anItem.discountType,
                'discount': anItem.discount,
                'status': anItem.itemStatus
            })
        }

        const itemsDeleteJson = { action: 'delete', purchase_items_id: [...itemIdsToBeDeleted] };


        const itemsUpdateJson = { action: 'update_multiple', purchase_order: purchaseOrderData.id, purchase_items: [] };
        for (let anItem of itemsToBeUpdated) {
            itemsUpdateJson.purchase_items.push({
                'id': anItem.id,
                'item': anItem.item_id,
                'quantity': anItem.quantity,
                'defective': anItem.defective,
                'non_discount_price': anItem.price,
                'discount_type': anItem.discountType,
                'discount': anItem.discount,
                'status': anItem.itemStatus
            })
        }


        console.log('to be updated', JSON.stringify(itemsUpdateJson))
        console.log('to be deleted', itemIdsToBeDeleted)
        console.log('to be added', itemsAddJson)


        setIng(true);

        try {

            let ladder = 0;

            //update the purchase items
            await updatePurchaseItem({ ...itemsUpdateJson })
                .then(res => {
                    console.log(res);
                    if (res.json.status) {
                        console.log('items updated************************')
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch(err => {
                    console.log(err);
                })

            //add the purchase items
            await createPurchaseItem({ ...itemsAddJson })
                .then((res) => {
                    console.log(res);
                    if (res.json.status) {
                        console.log('items added***********************************************');
                        ladder++
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err);
                });


            //delete the purchase Items
            await deletePurchaseItems({ ...itemsDeleteJson })
                .then((res) => {
                    console.log(res);
                    if (res.json.status) {
                        console.log('items deleted**********************************************')
                        ladder++
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });

            //update the purchase order
            await updatePurchaseOrder({ ...updateOrderJson })
                .then((res) => {
                    console.log(res);
                    if (res.json.status) {
                        console.log('purchase order updated successyly***********************');
                        ladder++
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err);
                });
            if (ladder === 4) {
                Jhilikka.fire({ icon: 'success', title: 'Purchase order updated successfully!', background: '#dffff0' })
                props.history.push('/purchase-order/' + props.match.params.id)
            }

        }
        catch (err) {
            console.log(err);
        }
        setIng(false)
    }

    // console.log(itemErrors)
    // console.log(purchaseOrderData);
    // console.log(purchaseItemsData);

    const PurchaseItemsList = purchaseItemsData.map((item, index) => {
        return (
            <tr key={index} className="tableInputRow">
                <th scope="row" >
                    <div
                        onClick={() => _removePurchaseItem(index, item.id)}
                        style={{ cursor: 'pointer', margin: '10px 0', border: '1px solid #f44236', borderRadius: '3px', width: '25px', textAlign: 'center' }}
                    >
                        <i className="feather icon-trash text-c-red f-15" />
                    </div>
                </th>

                <td className='table-input-long'>
                    <AsyncSelect
                        defaultOptions={defaultItemOptions}
                        value={isEmpty(purchaseItemsData[index].item_id) ? null : { label: purchaseItemsData[index].itemName, value: purchaseItemsData[index].item_id }}
                        onChange={(value) => _selectedItemChange(index, value)}
                        loadOptions={loadItemOptions}
                        placeholder='Start typing'
                        onInputChange={_itemInputChange}
                        className="mousetrap"
                        components={{
                            MenuList: CustomSelectMenuList,
                        }}
                        onMenuScrollToBottom={_scrollItemMenuToBottom}
                        onMenuClose={() => {
                            store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['item', false] })
                        }}
                        onMenuOpen={() => {
                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['item', true] })
                        }}
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].item_id : null}</ErrorBody>
                </td>

                <td className="table-input-short">
                    <Form.Control
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='quantity'
                        value={purchaseItemsData[index].quantity}
                        type="number"
                        placeholder="Qty"
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].quantity : null}</ErrorBody>
                </td>

                <td className="table-input-short">
                    <Form.Control
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='defective'
                        value={purchaseItemsData[index].defective}
                        type="number"
                        placeholder="Defective"
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].defective : null}</ErrorBody>
                </td>

                <td className="table-input-medium">
                    <Form.Control
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='price'
                        value={purchaseItemsData[index].price}
                        type="number"
                        placeholder="Price"
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].price : null}</ErrorBody>
                </td>
                <td className="table-input-short">
                    <Form.Control
                        readOnly
                        value={purchaseItemsData[index].itemSubtotal}
                        type="number"
                        placeholder="Sub Total"
                    />
                </td>
                <td className="table-input-medium">
                    <Form.Control
                        style={{ display: 'inline', 'maxWidth': 'calc( 50% - 1px )', marginRight: '1px', paddingLeft: '2px', paddingRight: '1px' }}

                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='discount'
                        value={purchaseItemsData[index].discount}
                        type="number"
                        placeholder="Discount value "
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].discount : null}</ErrorBody>

                    <Form.Control
                        style={{ display: 'inline', 'maxWidth': '50%', padding: '0 2px' }}
                        name='discountType'
                        value={purchaseItemsData[index].discountType}
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        as="select"
                    >
                        <option value='' disabled >Type</option>
                        <option value='percent'>%</option>
                        <option value='fixed'>Fixed</option>
                    </Form.Control>
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].discountType : null}</ErrorBody>

                </td>

                <td className="table-input-medium">
                    <Form.Control
                        readOnly
                        value={purchaseItemsData[index].total}
                        type="number"
                        placeholder="Total"
                    />
                </td>

                <td className="table-input-medium">
                    <Form.Control
                        style={{ padding: '0 2px' }}
                        name='itemStatus'
                        value={purchaseItemsData[index].itemStatus}
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        as="select"
                    >
                        <option value='' disabled >Status</option>
                        <option value='delivered'>Delivered</option>
                        <option value='incomplete'>Incomplete</option>
                        <option value='addedtocirculation'>In Circulation</option>
                    </Form.Control>
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].itemStatus : null}</ErrorBody>
                </td>

            </tr>
        )
    })

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Update Purchase Order</h5>
                            <hr />
                            <Row>
                                <Col sm={6} md={6} lg={6}>

                                    <Form.Group  >
                                        <Form.Label>Vendor Name*</Form.Label>
                                        <AsyncSelect
                                            className="mousetrap"
                                            placeholder='Start typing'
                                            value={purchaseOrderData.vendor_id ? { label: purchaseOrderData.vendorName, value: purchaseOrderData.vendor_id } : null}
                                            isClearable={true}
                                            cacheOptions={true}
                                            defaultOptions={defaultVendorOptions}
                                            loadOptions={loadVendorOptions}
                                            onChange={_selectedVendorChange}
                                            onInputChange={_vendorInputChange}
                                            components={{
                                                MenuList: CustomSelectMenuList,
                                            }}
                                            onMenuScrollToBottom={_scrollVendorMenuToBottom}
                                            onMenuClose={() => {
                                                store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['vendor', false] })
                                            }}
                                            onMenuOpen={() => {
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['vendor', true] })
                                            }}
                                        />
                                        <ErrorBody>{orderErrors.vendor_id}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={6} md={6} lg={6}>
                                    <Form.Group  >
                                        <Form.Label>Third Party Invoice No.*</Form.Label>
                                        <Form.Control name="invoiceNo" value={purchaseOrderData.invoiceNo || ''} onChange={_purchaseOrderDataChange} type="text" placeholder="Invoice No." />
                                        <ErrorBody>{orderErrors.invoiceNo}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={3} md={3} lg={3}>

                                    <Form.Group  >
                                        <Form.Label>Invoiced On</Form.Label> <br />
                                        <DatePicker onChange={_invoicedOnChange} placeholder="Pick Date" name="invoicedOn" selected={purchaseOrderData.invoicedOn} />
                                        <ErrorBody>{orderErrors.invoicedOn}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={3} md={3} lg={3}>
                                    <Form.Group  >
                                        <Form.Label>Completed On</Form.Label> <br />
                                        <DatePicker onChange={_completedOnChange} placeholder="Pick Date" name="completedOn" selected={purchaseOrderData.completedOn} />
                                        <ErrorBody>{orderErrors.completedOn}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={6} md={6} lg={6}>
                                    <Form.Label>Status*</Form.Label>
                                    <Form.Control name='orderStatus' value={purchaseOrderData.orderStatus} onChange={_purchaseOrderDataChange} as="select">

                                        <option value='' disabled >Select Order Status</option>
                                        {orderStatuses.map((item, idex) => {
                                            return <option key={idex} value={item.id}>{item.name}</option>
                                        })}
                                    </Form.Control>
                                    <ErrorBody>{orderErrors.orderStatus}</ErrorBody>
                                </Col>

                                <Col md={2}></Col>


                            </Row>

                            <Card.Header>
                                <Card.Title as="h5">Purchase Items</Card.Title>
                            </Card.Header>
                            <div >
                                <Table responsive>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th>Item Name*</th>
                                            <th>Quantity*</th>
                                            <th>Defective*</th>
                                            <th>Price*</th>
                                            <th>Sub Total</th>
                                            <th>Discount</th>
                                            <th>Total</th>
                                            <th>Status*</th>
                                        </tr>
                                    </thead>
                                    <tbody >

                                        {PurchaseItemsList}

                                        <tr>
                                            <th scope="row">
                                                <div onClick={_addPurchaseItem} style={{ cursor: 'pointer', textAlign: 'center', border: '1px solid #1de9b6', borderRadius: '3px', width: '25px' }}>
                                                    <i className="feather icon-plus text-c-green f-15" />
                                                </div>
                                            </th>
                                            <td colSpan={8}></td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <th scope="row" colSpan={6}>Sub Total</th>
                                            <th colSpan={2}>Rs. <Num>{purchaseOrderData.subTotal || 0}</Num></th>
                                        </tr>
                                        <tr>
                                            <th></th>
                                            <th scope="row" style={{ padding: '28px 0.75rem' }} colSpan={4}>Extra Discount</th>
                                            <td className="table-input-long" colSpan={2}>
                                                <Form.Control
                                                    style={{ display: 'inline', 'maxWidth': 'calc( 50% - 8px )', marginRight: '8px' }}
                                                    onChange={_purchaseOrderDataChange}
                                                    name='extraDiscount'
                                                    value={purchaseOrderData.extraDiscount}
                                                    type="number"
                                                    placeholder="Discount value "
                                                />
                                                <Form.Control
                                                    style={{ display: 'inline', 'maxWidth': '30%', padding: '0 2px' }}
                                                    name='extraDiscountType'
                                                    value={purchaseOrderData.extraDiscountType}
                                                    onChange={_purchaseOrderDataChange}
                                                    as="select"
                                                >
                                                    <option value='' disabled >Type</option>
                                                    <option value='percent'>%</option>
                                                    <option value='fixed'>Fixed</option>
                                                </Form.Control>
                                            </td>
                                            <th colSpan={2} style={{ padding: '28px 0.75rem' }}>Rs. <Num>{purchaseOrderData.extraDiscountAmount}</Num></th>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <th scope="row" colSpan={6} style={{ fontSize: '1.15em' }}>Total</th>
                                            <th colSpan={2} style={{ fontSize: '1.15em' }}>Rs. <Num>{purchaseOrderData.total}</Num></th>
                                        </tr>
                                        <tr style={{ height: '70px' }}>
                                            <th scope="row">{' '}</th>
                                            <td colSpan={8}></td>
                                        </tr>
                                    </tbody>
                                    {/* <br/>
                                        <br/> */}
                                </Table>
                            </div>


                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/purchase-order/' + props.match.params.id) }}
                                >
                                    Cancel
                                </Button>
                                <Button
                                    disabled={ing}
                                    variant="primary"
                                    onClick={_update}
                                >
                                    {ing ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Updating</> : 'Update'}

                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
                <DeleteModal
                    show={deleteData.showModal}
                    onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                    onDelete={() => _deletePurchaseItem()}
                />
            </Row>
        </Aux >
    );
}


export default Update;