import isEmpty from "./isEmpty"


const validatePurchaseOrderCreation = (purchaseOrderData) => {
    const { vendor_id, //will be a vendor object
        invoiceNo,
        invoicedOn,
        completedOn,
        orderStatus,
    } = purchaseOrderData



    const errors = {}
    if (isEmpty(vendor_id)) {
        errors.vendor_id = 'Vendor should not be empty!'
    }
    if (isEmpty(invoiceNo)) {
        errors.invoiceNo = 'Provide invoice no.!'
    }
    if (isEmpty(invoicedOn ? invoicedOn.toISOString() : null)) {
        errors.invoicedOn = 'This field can\'t be empty!'
    }
    if (isEmpty(completedOn ? completedOn.toISOString() : null)) {
        errors.completedOn = 'This field can\'t be empty!'
    }
    if (isEmpty(orderStatus)) {
        errors.orderStatus = 'Order Status can\'t be empty!'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}


const validatePurchaseItemsCreation = (purchaseItems) => {
    const errors = [];
    let i = 0;
    for (let anItem of purchaseItems) {
        console.log(anItem);
        const {
            item_id,
            quantity,
            defective,
            price,

            itemStatus,
        } = anItem;
        if (isEmpty(item_id)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].item_id = 'Can\'t be empty!'
        }
        if (isEmpty(quantity)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].quantity = 'Can\'t be empty!'
        }
        if (isEmpty(defective)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].defective = 'Can\'t be empty!'
        }
        if (isEmpty(price)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].price = 'Can\'t be empty!'
        }
        if (isEmpty(itemStatus)) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].itemStatus = 'Can\'t be empty!'
        }

        if (!isEmpty(quantity) && !isEmpty(defective) && defective > quantity) {
            errors[i] = !isEmpty(errors[i]) ? { ...errors[i] } : { ...{} }
            errors[i].defective = 'Can\'t be greater than quantity!'
        }


        i++;
    }
    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }

}


export { validatePurchaseOrderCreation, validatePurchaseItemsCreation }
